import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router'
import Mytab from './Mytab'

class Star extends Component {
	constructor(props) {
		super(props);
		this.state = ({
			item: {}
		})
	}
	render() {
		return (
			<div>
				{
					this.props.mydata.userinfo
						?
						(
							this.props.mydata.shoucang.map((item) => {
								this.setState({
									item: item
								})
								return (
									<div style={{ height: 150 }}>
										<image src={item.image} alt='/' style={{ width: '70%' }} />
										<button onclick={() => {
											this.props.dispatch({
												type: 'DEL_STAR',
												value: this.state.item
											})
										}}>删除</button>
									</div>
								)
							})
						)
						: (
							<Redirect
								to={{
									pathname: "/login"
								}}
							/>
						)
				}
				<Mytab/>
			</div>

		)
	}
}



const mapStateToProps = (state) => ({
	mydata: state.storage
})
export default connect(mapStateToProps)(Star);

