import { Carousel, WingBlank } from 'antd-mobile';
import React from 'react';

class Slide extends React.Component {
    state = {
        data: ['1', '2', '3','4','5'],
        imgHeight: 176,
    }
    componentDidMount() {
        // simulate img loading 模拟图片加载
        setTimeout(() => {
            this.setState({
                data: ['TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50','TB15tIjGVXXXXcoapXXSutbFXXX','O1CN019CAYMI1C4PK6mp9qX_!!27-0-luban','O1CN01rl6AUW1Cpa7QeEvPv_!!130-0-ppp-picassogw','O1CN01YdcfUK1D3mOfsf8TP_!!161-0-lubanu'],
            });
        }, 100);
    }
    render() {
        return (
            <WingBlank style={{marginTop:45}}>
                <Carousel
                    autoplay={true}
                    infinite
                    beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                    afterChange={index => console.log('slide to', index)}
                >
                    {this.state.data.map(val => (
                        <a
                            key={val}
                            href="http://www.alipay.com"
                            style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                        >
                            <img
                                src={`https://aecpm.alicdn.com/simba/img/${val}.jpg`}
                                alt=""
                                style={{ width: '100%', verticalAlign: 'top' }}
                                onLoad={() => {
                                    // fire window resize event to change height
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({ imgHeight: 'auto' });
                                }}
                            />
                        </a>
                    ))}
                </Carousel>
            </WingBlank>
        );
    }
}
export default Slide;

