import React from 'react'
import { connect } from 'react-redux'


const Login = (props) => {
    const login = ()=>{
        props.dispatch({
            type:'ADD_INFO',
            value:'ok'
        })
        props.history.push('/star')
    }
    return (
        <div >
            <input type="text" style={{ border: '1px solid',width:171,height:30,marginLeft:100,marginTop:50}} placeholder="请输入用户名"/>
            <div></div>
            <input type="password" style={{ border: '1px solid',width:171,height:30,marginTop:30,marginLeft:100}} placeholder="请输入密码" />
            <div></div>
            <button onClick={ login} style={{width:171,height:30,backgroundColor:'orange',marginTop:30,marginLeft:100}}>登录</button>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        mydata: state.storage
    }
}

export default connect(mapStateToProps)(Login)


