import React from 'react';
import { TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0
            }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-Homehomepagemenu'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-Homehomepagemenu1'></i>
                        }
                        selected={pathname === '/'}
                        onPress={() => {
                            this.props.history.push('/');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-star'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-star1'></i>
                        }
                        title="收藏"
                        key="star"
                        selected={pathname === '/star'}
                        onPress={() => {
                            this.props.history.push('/star')
                        }}
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-weibiao45127'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-List'></i>
                        }
                        title="订单列表"
                        key="list"
                        selected={pathname === '/store'}
                        onPress={() => {
                            this.props.history.push('/store')
                        }}
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-wode'></i>}
                        selectedIcon={<i className = "iconfont icon-wodedangxuan"></i>}
                        title="我的淘宝"
                        key="my"
                        selected={pathname === '/mine'}
                        onPress={() => { 
                            this.props.history.push('/mine')
                        }}
                    >

                    </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);