import React from 'react'
import { TabBar } from 'antd-mobile';
import './detail.css'
import { connect } from 'react-redux'

class Detail extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: '',
            data:{}
        };
    }
    render() {
        const id = this.props.match.params.id;
        return (
            <div className="detail">
                <i style={{ fontSize: 25 }} className='iconfont icon-Back' onClick={() => this.props.history.push('/')}></i>
                <div>
                    {
                        this.state.mydata.datalist.map((item) => {
                            if (item.id === id) {
                                this.setState({
                                    data:item
                                })
                                return (
                                    <img style={{ height: 500, width: '92%', marginLeft: 10, marginTop: 10 }} src={item.img} />
                                )
                            }
                            return;
                        })
                    }



                </div>
                <div style={{
                    position: 'fixed',
                    width: '100%',
                    bottom: 0
                }}>
                    <TabBar
                        unselectedTintColor="#949494"
                        tintColor="red"
                        barTintColor="white"
                    >
                        <TabBar.Item
                            title="店铺"
                            key="home"
                            icon={
                                <i className='iconfont icon-store'></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-store-checked'></i>
                            }
                            selected={this.state.selectedTab === 'blueTab'}
                            onPress={() => {
                                this.setState({
                                    selectedTab: 'blueTab',
                                });
                            }}
                        >
                        </TabBar.Item>
                        <TabBar.Item
                            icon={
                                <i className='iconfont icon-atm'></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-atm'></i>
                            }
                            title="客服"
                            key="service"
                        ></TabBar.Item>
                        <TabBar.Item
                            icon={
                                <i className='iconfont icon-star'></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-star1'></i>
                            }
                            selected={this.state.selectedTab === 'redTab'}
                            onPress={() => {
                                this.setState({
                                    selectedTab: 'redTab',
                                });
                                this.props.dispatch({
                                    type: 'ADD_STAR',
                                    value: this.state.data
                                })
                            }}
                            title="收藏"
                            key="star"
                            data-seed="logId1"
                        ></TabBar.Item>
                        <TabBar.Item
                            icon={
                                <i></i>
                            }
                            title="加入购物车"
                            key="cart"
                        >
                        </TabBar.Item>
                        <TabBar.Item
                            icon={
                                <i></i>
                            }
                            title="立即购买"
                            key="buy"
                        >
                        </TabBar.Item>
                    </TabBar>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        mydata: state.storage
    }
}

export default connect(mapStateToProps)(Detail)
