let obj = {
    datalist: [],
    shoucang: [],
    userinfo: '',
}
function storage(state=obj,action){
    if(action.type == 'ADD_LIST'){
        return [...state.datalist,action.value]
    }
    if(action.type == 'ADD_STAR'){
        return [...state.shoucang,action.value]
    }
    if(action.type == 'ADD_INFO'){
        state.userinfo = action.value;
    }
    if(action.type == 'DEL_STAR'){
        let arr = [...state.shoucang];
        arr.splice(action.index,1);
        return arr;
    }
    return state;
}

export default storage;