import React from 'react'
import Slide from './Slide'
import Mytab from './Mytab'
import './home.css'
import { connect } from 'react-redux'


class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            datalist: []
        }

    }
    componentDidMount() {
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url, {
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then(res => res.json())
            .then(data => {
                this.setState({
                    datalist: data.data
                })
                this.props.dispatch({
                    type: 'ADD_LIST',
                    value: data.data
                })
            }
            )


    }

    render() {
        return (
            <div style={{ position: 'relative', width: '100%', height: '100%' }}>
                <div style={{ top: 0, left: 0, zIndex: 1000, width: '100%', height: 40, backgroundColor: 'rgb(255,133,42)', position: 'fixed' }}>
                    <div style={{
                        display: 'block', width: '100%', height: 31,
                        backgroundRepeat: 'no-repeat', backgroundSize: '27px 30px', backgroundPosition: '6px -1px', backgroundImage: 'url("data:image/png;base64,")'
                    }}>
                        <div style={{ width: 300, marginTop: 5, backgroundColor: 'rgb(255,78,34)', height: 32, marginLeft: 50, borderRadius: 4 }} onClick={() => this.props.history.push('/search')}>
                            <span style={{ color: 'rgb(240,224,220)', marginLeft: 90 }}>寻找宝贝店铺</span>
                        </div>
                    </div>
                </div>
                <Slide />
                <div style={{ width: 371, display: 'flex', position: 'relative', flexWrap: 'wrap' }}>
                    {
                        this.state.datalist.map((item) => {
                            return(
                            <div style={{ height: 260, width: 171, marginLeft: 4.5, marginRight: 4.5, marginTop: 9, borderRadius: 6, overflow: 'hidden', position: 'relative' }} onClick={() => this.props.history.push('/detail/' + item.id)}>
                                <img src={item.img} style={{ width: 171, height: 150 }} />
                                <div style={{ height: 37, textAlign: 'center' }}>
                                    <span style={{ color: '#333' }}>{item.title}{item.id}</span>
                                </div>
                                <div style={{height: 20 }}>
                                    <span style={{ color: '#ff5500', fontSize: 11, marginLeft: 4 }}>¥</span>
                                    <span style={{ color: '#ff5500', fontSize: 14, marginLeft: 6 }}>{item.price}</span>
                                </div>
                            </div>
                        )
                    })
                    }
                </div>
                <Mytab />
            </div>)
    }

}

const mapStateToProps = (state) => {
    return {
        mydata: state.storage
    }
}

export default connect(mapStateToProps)(Home)

